import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import Switch from '@mui/material/Switch';
import BottomAppBar from '../components/BottomAppBar';

/* global platformClient */

function QueuePage(props) {
  const { onChange } = props;
  const me = useSelector((state) => state.me.me);
  const [checked, setChecked] = React.useState([]);
  const [userQueues, setUserQueues] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    if (me) {
      const routingApi = new platformClient.RoutingApi();
      const userId = me.id;
      const opts = {
        pageNumber: 1,
        pageSize: 100,
        sortOrder: 'asc',
      };
      routingApi.getUserQueues(userId, opts)
        .then((data) => {
          console.log(`getUserQueues success! data: ${JSON.stringify(data, null, 2)}`);
          const loadedUserQueues = data.entities;
          setUserQueues(loadedUserQueues);
          setChecked(
            loadedUserQueues.filter((queue) => queue.joined).map((queue) => queue.id),
          );
        })
        .catch((err) => {
          console.log('There was a failure calling getUserQueues');
          console.error(err);
        });
    }
  }, [me]);

  const handleNext = () => {
    const routingApi = new platformClient.UsersApi();
    const userId = me.id;
    const body = userQueues;
    const opts = {};
    routingApi.patchUserQueues(userId, body, opts)
      .then((data) => {
        console.log(`patchUserQueues success! data: ${JSON.stringify(data, null, 2)}`);
        navigate('/chat');
      })
      .catch((err) => {
        console.log('There was a failure calling patchUserQueues');
        console.error(err);
      });
  };

  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];
    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
    const newUserQueues = userQueues.map(
      (queue) => ({
        id: queue.id,
        joined: newChecked.indexOf(queue.id) !== -1,
      }),
    );
    onChange?.(newUserQueues);
  };

  return (
    <>
      <List
        subheader={<ListSubheader>Which queues are you working today?</ListSubheader>}
        sx={{ height: '100%', overflow: 'auto' }}
      >
        {
        userQueues.map((queue) => (
          <ListItem key={queue.id}>
            <ListItemText
              primary={queue.name}
              primaryTypographyProps={{ style: { textOverflow: 'ellipsis', overflow: 'hidden' } }}
            />
            <Switch
              edge="end"
              onChange={handleToggle(queue.id)}
              checked={checked.indexOf(queue.id) !== -1}
            />
          </ListItem>
        ))
      }
      </List>
      <BottomAppBar next={handleNext} />
    </>
  );
}

QueuePage.propTypes = {
  onChange: PropTypes.func,
};

QueuePage.defaultProps = {
  onChange: null,
};

export default QueuePage;
