import React, { Suspense } from 'react';
import { useRouteLoaderData, defer, Await } from 'react-router-dom';
import Box from '@mui/material/Box';
import CircularProgress from '@mui/material/CircularProgress';
import ErrorPage from './ErrorPage';
import Messages from '../components/Messages';
import { getConversationMessagesIds, messagesComparator } from '../helpers/gCloudHelpers';
import { getConversationMessages } from '../services/gcloud/conversationsService';

/* global platformClient */

const getConversationData = async (conversationId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const conversation = await conversationsApi.getConversation(conversationId);
    const messagesIds = getConversationMessagesIds(conversation);
    const messages = await getConversationMessages(conversationId, messagesIds);
    return {
      conversation,
      messages: messages.sort(messagesComparator),
    };
  } catch (err) {
    console.error(err);
    throw err;
  }
};

function ConversationPage() {
  const { conversationData } = useRouteLoaderData('conversation');
  return (
    <Box sx={{
      m: 1, height: '100%', display: 'flex', flexDirection: 'column',
    }}
    >
      <Suspense fallback={<CircularProgress />}>
        <Await resolve={conversationData} errorElement={<ErrorPage />}>
          <Messages />
        </Await>
      </Suspense>
    </Box>
  );
}

export async function loader({ params }) {
  const { conversationId } = params;
  return defer({
    conversationData: getConversationData(conversationId),
  });
}

export default ConversationPage;
