/* global platformClient */

const disconnectConversation = async (conversationId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = {
      state: 'DISCONNECTED',
    };
    await conversationsApi.patchConversationsMessage(
      conversationId,
      opts,
    );
  } catch (err) {
    console.error(err);
    throw err;
  }
};

const getConversationMessages = async (conversationId, messagesIds) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = {
      body: messagesIds,
      useNormalizedMessage: true,
    };
    const messages = await conversationsApi.postConversationsMessageMessagesBulk(
      conversationId,
      opts,
    );
    return messages.entities;
  } catch (err) {
    console.error(err);
    throw err;
  }
};

export { disconnectConversation, getConversationMessages };
