import React, { Suspense, useState } from 'react';
import {
  useNavigate, useRouteLoaderData, defer, Await,
} from 'react-router-dom';
import CircularProgress from '@mui/material/CircularProgress';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import ErrorPage from './ErrorPage';
import { conversationIsConnected, getCommunicationId } from '../helpers/gCloudHelpers';

/* global platformClient */

const getConversationWrapupData = async (conversationId, userId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const conversation = await conversationsApi.getConversation(conversationId);
    const meParticipant = conversation.participants
      .find((participant) => participant.userId === userId);
    const participantId = meParticipant.id;
    const communicationId = getCommunicationId(meParticipant);
    const wrapupCodes = await conversationsApi.getConversationsMessageParticipantWrapupcodes(
      conversationId,
      participantId,
    );
    return {
      conversation,
      participantId,
      communicationId,
      wrapupCodes,
    };
  } catch (err) {
    console.error(err);
    throw err;
  }
};

const wrapupConversation = async (conversationId, participantId, communicationId, wrapupCodeId) => {
  try {
    const conversationsApi = new platformClient.ConversationsApi();
    const opts = {
      body: {
        code: wrapupCodeId,
      },
    };
    await conversationsApi.postConversationsMessageParticipantCommunicationWrapup(
      conversationId,
      participantId,
      communicationId,
      opts,
    );
  } catch (err) {
    console.error(err);
    throw err;
  }
};

function ConversationWrapupPage() {
  const { conversationWrapupData } = useRouteLoaderData('conversation-wrapup');
  const navigate = useNavigate();
  return (
    <Box sx={{
      m: 1, height: '100%', display: 'flex', flexDirection: 'column',
    }}
    >
      <Suspense fallback={<CircularProgress />}>
        <Await resolve={conversationWrapupData} errorElement={<ErrorPage />}>
          {
            (loadedConversationWrapupData) => {
              const {
                conversation, participantId, communicationId, wrapupCodes,
              } = loadedConversationWrapupData;
              const [selectedWrapupCodeId, setSelectedWrapupCodeId] = useState();
              const handleButtonDoneClick = async () => {
                const conversationId = conversation.id;
                const wrapupCodeId = selectedWrapupCodeId;
                await wrapupConversation(
                  conversationId,
                  participantId,
                  communicationId,
                  wrapupCodeId,
                );
                const isConnected = conversationIsConnected(conversation);
                if (isConnected === true) {
                  navigate(`/conversations/${conversationId}`);
                } else {
                  navigate('/conversations');
                }
              };
              const handleListItemClick = (wrapupCodeId) => {
                setSelectedWrapupCodeId(wrapupCodeId);
              };
              return (
                <Box sx={{ width: '100%', display: 'flex', flexDirection: 'column' }}>
                  <List
                    subheader={<ListSubheader>After Call Work</ListSubheader>}
                    sx={{ height: '100%', overflow: 'auto' }}
                  >
                    {
                      wrapupCodes.map((wrapupCode) => (
                        <ListItemButton
                          key={wrapupCode.id}
                          selected={selectedWrapupCodeId === wrapupCode.id}
                          divider
                          onClick={() => handleListItemClick(wrapupCode.id)}
                        >
                          <ListItemText
                            primary={wrapupCode.name}
                            primaryTypographyProps={{ style: { textOverflow: 'ellipsis', overflow: 'hidden' } }}
                          />
                        </ListItemButton>
                      ))
                    }
                  </List>
                  <Button variant="contained" onClick={handleButtonDoneClick}>Done</Button>
                </Box>
              );
            }
          }
        </Await>
      </Suspense>
    </Box>
  );
}

export async function loader({ params }) {
  const { conversationId } = params;
  const userId = localStorage.getItem('userId');
  return defer({
    conversationWrapupData: getConversationWrapupData(conversationId, userId),
  });
}

export default ConversationWrapupPage;
