import React, { useEffect, useState } from 'react';
import { useAsyncValue, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import SendIcon from '@mui/icons-material/Send';
import { DateTime } from 'luxon';
import MessageBubble from './MessageBubble';
import ConversationToolBar from './ConversationToolbar';
import { groupBy } from '../helpers/arrayHelpers';
import { getConversationMessages, disconnectConversation } from '../services/gcloud/conversationsService';
import { getConversationMessagesIds, getCommunicationId, messagesComparator } from '../helpers/gCloudHelpers';

/* global platformClient */

const mergeMessages = async (messages, notifiedConversation, conversationId) => {
  let mergedMessages = messages;
  const messagesIds = messages.map((message) => message.id);
  const notifiedMessagesIds = getConversationMessagesIds(notifiedConversation);
  const newMessagesIds = notifiedMessagesIds.filter((id) => !messagesIds.includes(id));
  if (newMessagesIds.length > 0) {
    const newMessages = await getConversationMessages(conversationId, newMessagesIds);
    mergedMessages = mergedMessages.concat(newMessages);
    mergedMessages.sort(messagesComparator);
  }
  return mergedMessages;
};

function Messages() {
  const { conversation, messages: loadedMessages } = useAsyncValue();
  const [inputMessage, setInputMessage] = useState('');
  const [messages, setMessages] = useState(loadedMessages);
  const navigate = useNavigate();
  const notifiedConversation = useSelector((state) => state.notifications.conversation);

  const groupedMessages = groupBy(messages, (message) => DateTime.fromISO(message.timestamp).toFormat('dd LLL, yyyy'));

  useEffect(() => {
    const merge = async () => {
      if (notifiedConversation && notifiedConversation.id === conversation.id) {
        const mergedMessages = await mergeMessages(
          loadedMessages,
          notifiedConversation,
          conversation.id,
        );
        setMessages(mergedMessages);
      }
    };
    merge();
  }, [loadedMessages, notifiedConversation]);

  const handleInputMessageChange = (e) => {
    setInputMessage(e.target.value);
  };

  const handleDisconnect = async () => {
    await disconnectConversation(conversation.id);
    navigate('wrapup');
  };

  const handleWrapup = () => {
    navigate('wrapup');
  };

  const handleSendButtonClick = async () => {
    const userId = localStorage.getItem('userId');
    const conversationsApi = new platformClient.ConversationsApi();
    const conversationId = conversation.id;
    const meParticipant = conversation.participants
      .find((participant) => participant.userId === userId);
    const communicationId = getCommunicationId(meParticipant);
    const body = {
      textBody: inputMessage,
    };
    const opts = {
      useNormalizedMessage: true,
    };
    await conversationsApi.postConversationsMessageCommunicationMessages(
      conversationId,
      communicationId,
      body,
      opts,
    );
    setInputMessage('');
  };

  return (
    <>
      <ConversationToolBar
        conversation={conversation}
        onDisconnect={handleDisconnect}
        onWrapup={handleWrapup}
      />
      {Object.keys(groupedMessages).map((date) => (
        <Box key={date}>
          <Divider>{date}</Divider>
          <Box sx={{ display: 'flex', flexDirection: 'column' }}>
            {
              groupedMessages[date].map(
                (message) => (<MessageBubble key={message.id} message={message} />),
              )
            }
          </Box>
        </Box>
      ))}
      <Box sx={{ flexGrow: 1 }} />
      <Box sx={{ width: '100%', display: 'flex', flexDirection: 'row' }}>
        <TextField
          fullWidth
          autoComplete="off"
          value={inputMessage}
          onChange={handleInputMessageChange}
        />
        <IconButton
          disabled={inputMessage.length === 0}
          onClick={handleSendButtonClick}
        >
          <SendIcon />
        </IconButton>
      </Box>
    </>
  );
}

export default Messages;
