import React, { useEffect, useState } from 'react';
import { useAsyncValue, NavLink } from 'react-router-dom';
import { useSelector } from 'react-redux';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import ReactTimeAgo from 'react-time-ago';
import { conversationIsConnected } from '../helpers/gCloudHelpers';

const mergeConversation = (conversations, notifiedConversation) => {
  let updated = false;
  const mergedConversations = conversations.map((conversation) => {
    if (conversation.id === notifiedConversation.id) {
      updated = true;
      return notifiedConversation;
    }
    return conversation;
  });
  if (updated === false) {
    if (conversationIsConnected(notifiedConversation) === true) {
      mergedConversations.unshift(notifiedConversation);
    }
  }
  return mergedConversations;
};

/* notification api doesn´t contain startTime property */
const getConversationStartTime = (conversation) => {
  if (conversation.startTime) {
    return conversation.startTime;
  }
  return conversation.participants.map((participant) => participant.connectedTime).sort()[0];
};

function Conversations() {
  const loadedConversations = useAsyncValue();
  const [conversations, setConversations] = useState(loadedConversations);
  const notifiedConversation = useSelector((state) => state.notifications.conversation);

  useEffect(() => {
    if (notifiedConversation) {
      const mergedConversations = mergeConversation(loadedConversations, notifiedConversation);
      setConversations(mergedConversations);
    }
  }, [loadedConversations, notifiedConversation]);

  return (
    <List
      subheader={<ListSubheader>{conversations.length === 0 && 'Waiting for chats'}</ListSubheader>}
      sx={{ height: '100%', overflow: 'auto' }}
    >
      {
        conversations.map((conversation) => {
          const conversationStartTime = getConversationStartTime(conversation);
          return (
            <ListItem key={conversation.id} disablePadding component={NavLink} to={`/conversations/${conversation.id}`}>
              <ListItemButton divider>
                <ListItemText
                  primary={conversation.id}
                  primaryTypographyProps={{ style: { textOverflow: 'ellipsis', overflow: 'hidden' } }}
                />
                {conversationStartTime && <ReactTimeAgo date={new Date(conversationStartTime)} />}
              </ListItemButton>
            </ListItem>
          );
        })
      }
    </List>
  );
}

export default Conversations;
