import React, { useCallback, useEffect, useState } from 'react';
import { Outlet } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Box from '@mui/material/Box';
import useWebSocket, { ReadyState } from 'react-use-websocket';
import TopAppBar from './TopAppBar';
import { meActions } from '../store/me';
import { notificationsActions } from '../store/notifications';
import './Layout.css';

/* global platformClient */

function Layout() {
  const dispatch = useDispatch();
  const me = useSelector((state) => state.me.me);
  const [socketUrl, setSocketUrl] = useState();
  const [channelId, setChannelId] = useState();
  const [backgroundColor, setBackgroundColor] = useState();
  const { lastMessage, readyState } = useWebSocket(socketUrl, {}, socketUrl !== undefined);

  useEffect(() => {
    const usersApi = new platformClient.UsersApi();
    const opts = {};
    usersApi.getUsersMe(opts)
      .then((data) => {
        console.log(`getUsersMe success! data: ${JSON.stringify(data, null, 2)}`);
        dispatch(meActions.load(data));
      })
      .catch((err) => {
        console.log('There was a failure calling getUsersMe');
        console.error(err);
      });
  }, []);

  useEffect(() => {
    if (me) {
      const notificationsApi = new platformClient.NotificationsApi();
      notificationsApi.postNotificationsChannels()
        .then((data) => {
          console.log(`postNotificationsChannels success! data: ${JSON.stringify(data, null, 2)}`);
          setSocketUrl(data.connectUri);
          setChannelId(data.id);
        })
        .catch((err) => {
          console.log('There was a failure calling postNotificationsChannels');
          console.error(err);
        });
    }
  }, [me]);

  useEffect(() => {
    if (channelId && readyState === ReadyState.OPEN) {
      const notificationsApi = new platformClient.NotificationsApi();
      const body = [`v2.users.${me.id}.conversations`];
      const opts = {};
      notificationsApi.putNotificationsChannelSubscriptions(channelId, body, opts)
        .then((data) => {
          console.log(`putNotificationsChannelSubscriptions success! data: ${JSON.stringify(data, null, 2)}`);
        })
        .catch((err) => {
          console.log('There was a failure calling putNotificationsChannelSubscriptions');
          console.error(err);
        });
    }
  }, [channelId, readyState]);

  useEffect(() => {
    if (lastMessage) {
      const data = JSON.parse(lastMessage.data);
      const topic = data.topicName;
      if (topic.indexOf('conversations') !== -1) {
        const { eventBody } = data;
        dispatch(notificationsActions.process(eventBody));
      }
    }
  }, [lastMessage]);

  useEffect(() => {
    console.log(readyState);
    switch (readyState) {
      case ReadyState.OPEN:
        setBackgroundColor('#FF4D22');
        break;
      default:
        setBackgroundColor('transparent');
        break;
    }
  }, [readyState]);

  return (
    <Box className="App">
      <Box sx={{ height: '5px', backgroundColor }} />
      <TopAppBar title="" />
      <Outlet />
    </Box>
  );
}

export default Layout;
