import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import { Link } from 'react-router-dom';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Avatar from '@mui/material/Avatar';
import Divider from '@mui/material/Divider';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';

/* global platformClient */

const getAvatarImage = (user) => {
  if (user && user.images?.length > 0) {
    return user.images[0].imageUri;
  }
  return null;
};

function TopAppBar(props) {
  const { title } = props;
  const me = useSelector((state) => state.me.me);
  const [onQueue, setOnQueue] = useState(false);
  const [userMenuAnchorEl, setUserMenuAnchorEl] = useState(null);
  const [menuAnchorEl, setMenuAnchorEl] = useState(null);
  const [systemPresences, setSystemPresences] = useState();

  const avatarSrc = getAvatarImage(me);

  useEffect(() => {
    const presenceApi = new platformClient.PresenceApi();
    presenceApi.getSystempresences()
      .then((data) => {
        setSystemPresences(data);
      }).catch((err) => {
        console.error(err);
        throw err;
      });
  }, []);

  useEffect(() => {
    if (me && systemPresences) {
      try {
        const presenceApi = new platformClient.PresenceApi();
        let presenceDefinitionId;
        if (onQueue === true) {
          presenceDefinitionId = systemPresences.find((systemPresence) => systemPresence.name === 'ON_QUEUE').id;
        } else {
          presenceDefinitionId = systemPresences.find((systemPresence) => systemPresence.name === 'OFFLINE').id;
        }
        const body = {
          presenceDefinition: {
            id: presenceDefinitionId,
          },
        };
        presenceApi.patchUserPresencesPurecloud(
          me.id,
          body,
        );
      } catch (err) {
        console.error(err);
        throw err;
      }
    }
  }, [onQueue, me, systemPresences]);

  const handleMenu = (event) => {
    setMenuAnchorEl(event.currentTarget);
  };

  const handleLogout = async () => {
    const clientId = 'ec8d20f0-a7d3-481a-9512-5ec77db14554';
    // const redirectUri = 'http://localhost:3000/';
    const redirectUri = 'https://mario-genesys.github.io/';
    window.location.replace(`https://login.mypurecloud.com/logout?client_id=${clientId}&redirect_uri=${redirectUri}`);
  };

  const handleMenuClose = () => {
    setMenuAnchorEl(null);
  };

  const handleUserMenu = (event) => {
    setUserMenuAnchorEl(event.currentTarget);
  };

  const handleUserMenuClose = () => {
    setUserMenuAnchorEl(null);
  };

  const handleOnQueueChange = (event) => {
    setOnQueue(event.currentTarget.checked);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            size="large"
            onClick={handleMenu}
            edge="start"
            color="inherit"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            anchorEl={menuAnchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            open={Boolean(menuAnchorEl)}
            onClose={handleMenuClose}
          >
            <MenuItem onClick={handleMenuClose} component={Link} to="/queues">Queues</MenuItem>
            <MenuItem onClick={handleMenuClose} component={Link} to="/conversations">Conversations</MenuItem>
          </Menu>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            {title}
          </Typography>
          <div>
            <IconButton
              size="large"
              onClick={handleUserMenu}
              color="inherit"
            >
              <Avatar src={avatarSrc} />
            </IconButton>
            <Menu
              anchorEl={userMenuAnchorEl}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(userMenuAnchorEl)}
              onClose={handleUserMenuClose}
            >
              <FormControlLabel
                sx={{ ml: 1, mb: 1 }}
                control={<Switch checked={onQueue} onChange={handleOnQueueChange} />}
                label={onQueue ? 'On Queue' : 'Off Queue'}
              />
              <Divider />
              <MenuItem onClick={handleLogout}>Logout</MenuItem>
            </Menu>
          </div>
        </Toolbar>
      </AppBar>
    </Box>
  );
}

TopAppBar.propTypes = {
  title: PropTypes.string,
};

TopAppBar.defaultProps = {
  title: '',
};

export default TopAppBar;
